import React from 'react';
import images from './../scripts/imgimporter';

const SectionContainer = props =>

    <section className="section-container">
        <div className="section-body">
            {
                props.section.position !== 0?
                    <div className="row">
                        <div className="section-title">
                            <h2>{props.section.title}</h2>
                        </div>
                        <div className="section-text" dangerouslySetInnerHTML={{__html: props.section.text }} />
                        {
                            props.section.image !== null?
                                <img alt={props.section.image} src={images[props.section.image]} style={props.section.imageFullWidth===true?{width:'100%'}:{}}/>
                            :''
                        }
                    </div>
                :
                    <div className="column">
                        <div className={"text-container " + (props.section.direction === 0 ? "right" : "left")}
                            style={props.section.direction===0?{order:1}:{}}>
                            <div className="section-title">
                                <h2>{props.section.title}</h2>
                            </div>
                            <div>
                            <div className="section-text" dangerouslySetInnerHTML={{__html: props.section.text }} />
                            </div>
                        </div>
                        <div className="image-container">
                            <img style={props.section.direction===0?{float: 'unset'}:{}} alt={props.section.image} src={images[props.section.image]}/>
                        </div>
                    </div>
            }
            
        </div>
    </section>;

export default SectionContainer;