import React, { Component } from 'react';
import '../App.scss';
import SectionContainer from './sectioncontainer';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class ProjectDetail extends Component{

 constructor() {
    super();
    this.state = {detail: null} 

}

render() {
    let detail = this.props.detail;
    return (
        <div>
        {
           (detail != null && this.props.showDetail)?
                <div>
                    <section className="detail-banner-container">
                        <div className="detail-banner-img"
                            style={{backgroundImage:"url(" + detail.image + ")"}}>
                            <div className="detail-banner-shadow"></div>
                            <div className="detail-banner-text-container">
                                <div className="detail-banner-title"><h1>{detail.title}</h1></div>
                                <div className="detail-banner-description"><p>{detail.description}</p></div>        
                                <div className="detail-banner-techs">
                                {
                                    detail.techs.map((tech, index) => {
                                        return <span 
                                        key={index}>
                                            {tech}
                                        </span>
                                    })
                                }
                                </div>     
                                <div className="detail-banner-buttons">
                                    {
                                        detail.demo !== null?
                                        <button onClick={this.props.openLink.bind(this, detail.demo)} className="demo">Live Demo</button> 
                                        :''                                         
                                    }    
                                    {
                                        detail.github !== null?
                                        <span className="buttonLink">
                                            <a  title="github" onClick={this.props.openLink.bind(this, detail.github)}>
                                                <FontAwesomeIcon className="github" icon={['fab', 'github']} />
                                            </a>
                                        </span>     
                                        :''
                                    }  
                                </div>
     
                                
                                <div className="detail-banner-buttons">
                                    <button title="Previous Project" className="back" onClick={this.props.stepProject.bind(this, -1)}>
                                        <FontAwesomeIcon icon={['fa', 'angle-left']} /><span> Prev</span>
                                    </button>    
                                    <button title="Next Project" className="next" onClick={this.props.stepProject.bind(this, 1)}>
                                        <span>Next </span><FontAwesomeIcon icon={['fa', 'angle-right']} />
                                    </button>  
                                </div>                                    
                            </div>

                        </div>         
                    </section>      
                    {
                        detail.sections.map((section, index) => {
                            return <SectionContainer 
                            key={index}
                            section={section}
                            />
                        })
                    }             
                </div>
                :''
        }
        </div>
    );
  }


}
export default ProjectDetail;