import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import images from './../scripts/imgimporter';

const Navigation = props =>

    <section className="navigation">
        <div className="nav-container">
    <div className="brand">
        {/* <span>
            <a href="https://anthonyolores.github.io/portfolio/" title="Home Page">
                <img src={images['logo']}/>
            </a>
        </span> */}
        <span><a title="home" href="https://anthonyolores.github.io/portfolio/"><FontAwesomeIcon icon={['fa', 'home']} /></a></span>
        <span><a title="linkedin" target="_blank" href="#"><FontAwesomeIcon icon={['fab', 'linkedin-in']} /></a></span>
        <span><a title="github" target="_blank" href="https://github.com/anthonyolores?tab=repositories"><FontAwesomeIcon icon={['fab', 'github']} /></a></span>
        {/* <span><a title="behance" href="#"><FontAwesomeIcon icon={['fab', 'behance']} /></a></span> */}
    </div>
    <nav>
        <div className="nav-mobile"><a id="nav-toggle" href="#!"><span></span></a></div>
        <ul className="nav-list">
            <li>
                <a href="#!">Projects</a>
                <ul className="nav-dropdown">
                {
                    props.projects.map((project, index) => {
                        return <li onClick={props.showDetail.bind(this, index)}>
                                <a href="#">
                                    {project.navTitle}
                                </a>
                            </li>
                    })
                }
                {/* <li>
                    <a href="#!">Web Design</a>
                </li>
                <li>
                    <a href="#!">Web Development</a>
                </li>
                <li>
                    <a href="#!">Graphic Design</a>
                </li>
                </ul> */}
                </ul>
            </li>
        </ul>
    </nav>
    </div>
    </section>;

export default Navigation;